import React, {useEffect, useState} from 'react';
import {Container, Row, Col} from "react-bootstrap";

const Hola = () => {
	const [scroll, setScroll] = useState(0);
	
	
	const listenToScroll = () => {
		const winScroll =
			document.body.scrollTop || document.documentElement.scrollTop
		
		const height =
			document.documentElement.scrollHeight -
			document.documentElement.clientHeight
		
		const scrolled = winScroll / height
		
		setScroll(scrolled)
	};
	
	useEffect(() => {
		if (typeof window !== `undefined`) {
			window.addEventListener('scroll', listenToScroll);
		}
		
		return () => window.removeEventListener('scroll', listenToScroll)
		
	},[]);
	
	useEffect(() => {
		console.log('scroll', scroll)
		
	},[scroll]);
	
	return (
			<div>
			<Container>
				<Row>
					<Col style={{padding: '7%'}}>
						<p style={{
							fontSize: '1.5rem',
							fontWeight: 'bold',
							color: `rgb(${scroll * 50 + 200}, ${scroll * 20 + 0}, ${scroll * 200}`,
							fontFamily: 'Business'
						}}>
							Yerba Mala es una publicación impresa anualmente desde 2017,
							creada y editada por Pedro Ponce (
							<span style={{backgroundColor: 'black'}}>@nedtemp</span>
							), en la Ciudad de México.
							En ésta se recopilan las obras de las personas que han participado a
							través de las convocatorias y se han incluido trabajos de fotografía,
							poesía, cuento corto, ilustración, música, entre otros.
							<br/>
							La revista o
							“fanzine” se ha presentado en Ciudad de México y Puebla, Berkeley, Oakland
							y San Francisco en California, Filadelfia PA y el año pasado en Bogotá, Colombia :-)
						</p>
					</Col>
				</Row>
			</Container>
			</div>
	);
};

export default Hola;
